export default order => {

  let basket = '';
  if (order.basket && order.basket.length) {
    order.basket.forEach(({ status, name, weight, count, price }) => {
      if (!status) return;
      basket += `<tr class="modal__order-item">
        <td class="modal__order-name">${name}</td>
        <td class="modal__order-weight">${weight}</td>
        <td class="modal__order-count">${count}</td>
        <td class="modal__order-price">${price.toFixed(2)} Byn</td>
      </tr>`;
    });

    basket += `<tr class="modal__order-item delivery-row">
      <td class="modal__order-name">Доставка</td>
      <td class="modal__order-weight"></td>
      <td class="modal__order-count"></td>
      <td class="modal__order-price">${order.deliveryCost} Byn</td>
    </tr>`;
  }

  let comments = '';
  if (order.comments) {
    comments = `<tr>
      <td>Комментарии</td>
      <td>${order.comments}</td>
    </tr>`;
  }

  let delivery = '';
  if (order.deliveryMethod === 'Доставка') {
    delivery = `<tr>
      <td>Адрес</td>
      <td>${order.address}</td>
    </tr>
    <tr>
      <td>Время доставки</td>
      <td>${order.time}</td>
    </tr>
    <tr>
      <td>Способ оплаты</td>
      <td>${order.paymethod}</td>
    </tr>`;
  }

  const html = `<div class="modal__order">
    <div class="modal__order-title">Заказ № ${order.number}</div>
    <div class="modal__order-table">
      <table>
        <tbody>
          <tr>
            <td>Дата и время заказа</td>
            <td>${order.date}</td>
          </tr>
          <tr>
            <td>Способ доставки</td>
            <td>${order.deliveryMethod}</td>
          </tr>
          ${delivery}
          <tr>
            <td>Номер телефона</td>
            <td>${order.phone}</td>
          </tr>
          ${comments}
        </tbody>
      </table>
    </div>
    <div class="modal__order-title">Состав заказа</div>
    <div class="modal__order-basket">
      <table>
        <tbody>
          ${basket}
        </tbody>
      </table>
    </div>
    <div class="modal__order-title">ИТОГО: ${(order.price * (100 - order.discount) / 100).toFixed(2)} Byn</div>
    <div class="modal__order-footer">
      <button class="btn btn--icon js-modal-order-submit" type="button">
        <span class="btn__name">Подтвердить</span>
        <span class="svg-icon svg-icon--arrow-right">
          <svg class="svg-icon__symbol" width="9" height="14">
            <use xlink:href="#icon-arrow-right"></use>
          </svg>
        </span>
      </button>
      <div class="modal__order-note">Нажимая кнопку «Перейти к оплате» вы соглашаетесь с <a href="#" target="_blank">договором оферты</a></div>
    </div>
  </div>`;
  return html;
}

