import Swiper from 'swiper/bundle';
import sliderReviews from './slider-reviews';
import AccordionsGroup from './elements/accordionsGroup';
import DetoxOrder from './elements/detoxOrder';
import Promocode from './elements/promocode';
import Recipe from './elements/recipe';
import detoxCatalogSlider from "./detox-catalog-slider";
import sliderTab from './slider-tab';


const detox = () => {

  const detox = document.querySelector('.js-detox');

  if (!detox) return;

  // order
  const detoxOrder = new DetoxOrder('.js-detox-order');
  detoxOrder.init();

  // section details - tabs
  const detailTabs = detox.querySelector('.js-detail-tabs');
  const detailTabsButtons = Array.prototype.slice.call(detailTabs.querySelectorAll('[data-tabs="button"]'));
  const detailBody = detailTabs.querySelector('[data-tabs="body"]');
  const detailBodyTabs = Array.prototype.slice.call(detailBody.querySelectorAll('[data-tabs="tab"]'));
  let active;

  const setActive = a => {
    detailTabsButtons[a].classList.add('active');
    detailBodyTabs[a].classList.add('active');
    active = a;
  }

  detailTabsButtons.forEach((button, i) => {
    if (button.classList.contains('active')) setActive(i);
    button.addEventListener('click', () => {
      detailTabsButtons[active].classList.remove('active');
      detailBodyTabs[active].classList.remove('active');
      setActive(i);
    });
  });

  // // линк "подробнее"
  // const linkDetail4 = detox.querySelector('.js-go-detox-detail-4');
  // linkDetail4.addEventListener('click', e => {
  //   e.preventDefault();
  //   detailTabsButtons[3].click();
  //   window.location = '#detox-detail';
  // })

  // accordions
  const accordionsGroup = new AccordionsGroup('.js-detox-accordions-group');
  accordionsGroup.init();

  // slider
  const SliderReviews = new Swiper('.js-slider-banner', sliderReviews);
  const SliderTab = new Swiper('.detox__detail-buttons', sliderTab);


  const reviewForm = document.querySelector('.js-modal-detox-review');
  reviewForm.addEventListener('submit', e => {
    e.preventDefault();
    e.stopPropagation();
    const data = new FormData(reviewForm);

    // console.log(data.get('detox-modal-name'));
    // console.log(data.get('detox-modal-phone'));
    console.log(data.get('detox-modal-text'));

    const modal = document.querySelector('#detoxSendReview .modal__content');
    modal.classList.add('text-center');
    modal.style.width = '460px';
    modal.innerHTML = `<div class="modal__header">
      <h2 class="title modal__title">Спасибо!</h2>
      <p>Ваш отзыв отправлен.</p>
    </div>`;
    const timerId = setTimeout(() => {
      window.MODAL.closeAllModals();
      clearTimeout(timerId);
    }, 3000);
  });

  // PROMOCODE
  const cbPromocode = (discount) => {
    console.log('discount', discount);
  }

  // init promocode
  const promocode = new Promocode('.js-detox-input-promocode');
  promocode.options({ cb: cbPromocode }).init();

  // Resipe block open
  Recipe();

  //DetoxCatalog

  // const detoxCatalogSlider = document.querySelector('.detox__catalog-slider');
  const detoxCatalogSliderInner = document.querySelector('.detox__catalog-slider_inner');
  const detoxCatalogSliderItem = document.querySelectorAll('.detox__catalog-slider_item');
  const detoxCatalogCard = document.querySelectorAll('.detox__catalog-card');

  const toggleDetox = (index) => {
    for (let i = 0; i < detoxCatalogCard.length; i++) {
      if (index === i) {
        detoxCatalogCard[i].classList.remove('is-hide');
        detoxCatalogSliderItem[i].classList.add('is-active');
      } else {
        detoxCatalogCard[i].classList.add('is-hide');
        detoxCatalogSliderItem[i].classList.remove('is-active');
      }
    }
  }

  detoxCatalogSliderInner.addEventListener('click', (e) => {
    const target = e.target.closest('.detox__catalog-slider_item');
    detoxCatalogSliderItem.forEach((item, i) => {
      if (item === target) {
        toggleDetox(i);
      }
    })
  });

  const DetoxCatalogSlider = new Swiper('.detox__catalog-slider', detoxCatalogSlider);


  //detox-modal-tab
  const modalDetoxList = document.querySelector('.modal__detox-list');
  const detoxSetDetail = document.querySelector('#detoxSetDetail');
  const modalDetoxListTooltip = document.querySelectorAll('.modal__detox-list_tooltip');
  const modalDetoxListPopup = document.querySelectorAll('.modal__detox-list_popup');

  const toggleDetoxSoup = (index) => {
    for (let i = 0; i < modalDetoxListPopup.length; i++) {
      if (index === i) {
        modalDetoxListPopup[i].classList.add('active');
      } else {
        modalDetoxListPopup[i].classList.remove('active');
      }

      detoxSetDetail.addEventListener('click', (e) => {
        if (e.target.closest('.modal__detox-list_popup-cross') || e.target.matches('.modal__detox-list_popup')
        || e.target.matches('.modal') || e.target.closest('.modal__close')) {
          modalDetoxListPopup[i].classList.remove('active');
        }
      });
    }
  }

  modalDetoxList.addEventListener('click', (e) => {
    if (e.target.closest('.modal__detox-list_tooltip')) {
      modalDetoxListTooltip.forEach((item, i) => {
        if (item === e.target) {
          toggleDetoxSoup(i)
        }
      })
    }
  });

  

};

export default detox;
