export default class DetoxOrder {

  constructor(currentTag, parentNode = document) {
    this.wrapper = parentNode.querySelector(currentTag);
    this.active = [];
  }

  init() {
    if (this.wrapper) this._wrapperInit();
    return this;
  }

  _wrapperInit() {
    this.sets = Array.prototype.slice.call(this.wrapper.querySelectorAll('[data-set]'));
    this._initSets();

    this.form = this.wrapper.querySelector('[data-detox-order="form"]');

    this.buttonSubmit = this.form.querySelector('[data-detox-order="button-submit"]');

    this.form.addEventListener('submit', (e) => {
      e.preventDefault();
      // const data = new FormData(this.form);
      // console.log('data', data);
      const paymentBePaid = this.form.querySelector('#pay-method-3');
      // if (paymentBePaid.checked) {
      //   window.location = 'https://www.google.com/search?q=%D0%BA%D0%B0%D0%BA+%D0%BF%D0%BE%D0%B4%D0%BA%D0%BB%D1%8E%D1%87%D0%B8%D1%82%D1%8C+bepaid&ei=WtuwYYS2FoWExc8PtY2N4Ak&ved=0ahUKEwiEy774zdT0AhUFQvEDHbVGA5wQ4dUDCA4&uact=5&oq=%D0%BA%D0%B0%D0%BA+%D0%BF%D0%BE%D0%B4%D0%BA%D0%BB%D1%8E%D1%87%D0%B8%D1%82%D1%8C+bepaid&gs_lcp=Cgdnd3Mtd2l6EAMyBQghEKABOgcIABBHELADOgcIABCwAxBDOgoILhDIAxCwAxBDOgUIABCABDoFCC4QgAQ6CwgAEIAEELEDEIMBOggIABCABBCxAzoKCAAQsQMQgwEQQzoOCC4QgAQQsQMQxwEQowI6CAgAELEDEIMBOggILhCABBCxAzoFCAAQsQM6CAgAEIAEEMkDOgUIABCSAzoECAAQCkoECEEYAFCYEVjGpgFg6bIBaARwAngEgAG-AYgByB6SAQQwLjMxmAEAoAEBsAEAyAELwAEB&sclient=gws-wiz'
      // } else {
      //   window.MODAL.showModal(document.querySelector('#detoxOrderThanks'));
      // }
    });
  }

  _initButtonSubmit() {
    if (this.active.length) this.buttonSubmit.removeAttribute('disabled');
    else this.buttonSubmit.setAttribute('disabled', true);
  }

  _changeActive(i) {
    let res;
    const ind = this.active.findIndex(el => el === i);
    if (ind >= 0) {
      this.active.splice(ind, 1);
      res = false;
    } else {
      this.active.push(i);
      res = true;
    }
    this._initButtonSubmit();
    return res;
  }

  _initSets() {
    const modalDetail = document.querySelector('#detoxSetDetail');
    const buttonCheck = modalDetail.querySelector('[data-detox-modal="button"]');
    const fieldNum = modalDetail.querySelector('.field-num');
    const fieldNumInputModal = modalDetail.querySelector('.field-num__input');
    const detoxBoxes = document.querySelector('.detox-boxes');
    const detoxBox = document.querySelector('.detox-box');
    // const detoxResultInfoCount = document.querySelector('.detox__result-info_count');
    // const detoxResultInfoCountBig = document.querySelector('.detox__result-info_count-big');
    const detoxResultList = document.querySelector('.detox__result-list');

    detoxResultList.addEventListener('click', (e) => {
      if (e.target.closest('.small-box')) {
        // detoxResultInfoCount.textContent = 0 + 'шт';
        detoxBox.value = 0;
      } else if (e.target.closest('.small-boxes')) {
        // detoxResultInfoCountBig.textContent = 0 + 'шт';
        detoxBoxes.value = 0;
      }
    });
    
    let currentSet;

    modalDetail.addEventListener('click', () => {
      const fieldNumInput = this.sets[currentSet].querySelector('.field-num__input');
      const detoxSet = this.sets[currentSet];
      fieldNumInput.value = fieldNumInputModal.value;
      if (fieldNumInput.value == 0) {
        detoxSet.classList.remove('active');
      }
    });


    buttonCheck.addEventListener('click', () => {
      this.sets[currentSet].classList[this._changeActive(currentSet) ? 'add' : 'remove']('active');
      const fieldNumInput = this.sets[currentSet].querySelector('.field-num__input');
      fieldNumInput.value = fieldNumInputModal.value;
      window.MODAL.closeAllModals();
    });

    fieldNum.addEventListener('click', () => {
      if (fieldNumInputModal.value == 0) {
        const detoxSet = modalDetail.querySelector('.js-detox-button-help');
        detoxSet.classList.remove('checked');
        
      }
    })

    this.sets.forEach((set, i) => {
      const button = set.querySelector('.js-data-set-button');
      const fieldNumInput = set.querySelector('.field-num__input');
      const detoxSetAction = set.querySelector('.field-num');

      button.addEventListener('click', () => {
        set.classList[this._changeActive(i) ? 'add' : 'remove']('active');
        fieldNumInputModal.value = fieldNumInput.value;
      });

      detoxSetAction.addEventListener('click', () => {
        const detoxSet = set;
        // detoxResultInfoCount.textContent = detoxBox.value + 'шт';
        // detoxResultInfoCountBig.textContent = detoxBoxes.value + 'шт';
        if (fieldNumInput.value == 0) {
          detoxSet.classList.remove('active');
        }
      });

      set.addEventListener('click', e => {
        if (e.target.closest('.detox-set__head') || e.target.closest('.detox-set__image')) {
          currentSet = i;
          modalDetail.querySelector('[data-detox-modal="name"]').innerText = set.querySelector('.detox-set__title').innerText;
          if (this.active.includes(i)) buttonCheck.classList.add('checked');
          else buttonCheck.classList.remove('checked');
          fieldNumInputModal.value = fieldNumInput.value;
          window.MODAL.showModal(modalDetail);
        }
      });
    });
  }

  getActives() {
    return this.active.map(i => this.sets[i].getAttribute('data-set'));
  }
}
