export default class FixedElement {

  constructor(currentTag, parentNode = document) {
    this.wrapper = parentNode.querySelector(currentTag);
  }

  init() {
    if (this.wrapper) this._wrapperInit();
    return this;
  }

  _wrapperInit() {
    this.wrapperHeight = this.wrapper.clientHeight;
    this.element = this.wrapper.querySelector('[data-fixed="element"]');
    this.elementHeight = this.element.clientHeight;

    this._setElementTop();

    window.addEventListener('scroll', () => {
      if (window.outerWidth > 768) this._setElementTop();
    });

    const mediaQuery = window.matchMedia('(max-width: 768px)');
    mediaQuery.addEventListener('change', this._handleTabletChange.bind(this));
    this._handleTabletChange();

  }

  _handleTabletChange(e) {
    if (e && e.matches) {
      this.element.removeAttribute('style');
    }
  }

  _setElementTop() {
    if (window.pageYOffset < (this.wrapperHeight - this.elementHeight + 250))
      this.element.style.top = window.pageYOffset + 'px';
  }
}
