import Swiper from 'swiper/bundle';
import DOMReady from './utils/DOMReady';
import sliderBanner from './modules/slider-banner';
import sliderSoup from './modules/slider-soup';
import sliderFit1 from './modules/slider-fit1';
import sliderFit2 from './modules/slider-fit2';
import sliderFit3 from './modules/slider-fit3';
import sliderDetoxBanner from './modules/slider-detox-banner';
import fieldNum from './modules/field-num';
import modal from './modules/modal';
import order from './modules/order';
import phones from './modules/phones';
import basketOpen from './modules/basket-open';
import initBurger from './modules/burger';
import toTop from './modules/to-top';
import modalChangeAddress from './modules/elements/modalChangeAddress';
import initSingIn from './modules/modal-sing-in';
import BasketActions from './modules/basketActions';
import detox from './modules/detox';


DOMReady(() => {
  console.log('init app');

  (function fixedHeader(params) {

    if (document.querySelector('.js-header')) {
      const header = document.querySelector('.js-header');

      window.addEventListener('scroll', () => {
        header.classList.toggle('is-fixed', window.scrollY > 150);
      });
    } else {
      return false;
    }

  }());

  const SliderBanner = new Swiper('.js-slider-banner', sliderBanner);
  const SliderSoup = new Swiper('.js-slider-soup', sliderSoup);
  const SliderFit1 = new Swiper('.js-slider-fit1', sliderFit1);
  const SliderFit2 = new Swiper('.js-slider-fit2', sliderFit2);
  const SliderFit3 = new Swiper('.js-slider-fit3', sliderFit3);
  const SliderDetoxBanner = new Swiper('.js-slider-detox-banner', sliderDetoxBanner);

  // Открытие корзины
  basketOpen();
  // Счетчик количества
  fieldNum();
  // Открытие модальных окон
  window.MODAL = modal();

  // Открытие мобильного меню
  initBurger();

  // Кнопка вверх
  toTop();

  const mca = document.querySelector('.js-modal-change-address');
  if (mca) {
    window.ModalChangeAddress = new modalChangeAddress(mca);
    window.ModalChangeAddress.init();
  }

  phones();

  order();

  initSingIn();

  const basketActions = new BasketActions();
  basketActions.init();

  detox();

});
