export default class AccordionsGroup {

  constructor(currentTag, parentNode = document) {
    this.wrapper = parentNode.querySelector(currentTag);
    this.active = null;
  }

  init() {
    if (this.wrapper) this._wrapperInit();
    return this;
  }

  options({ counts = 6 }) {
    this.counts = counts;
  }

  _wrapperInit() {
    this.accordions = Array.prototype.slice.call(this.wrapper.querySelectorAll('[data-accordion="accordion"]'));
    this.accordionsCols = Array.prototype.slice.call(this.wrapper.querySelectorAll('[data-accordions-col]'));
    this.buttonUploadMore = document.querySelector('[data-accordions="upload-more"]');

    this.accordions.forEach((accordion, i) => {
      const button = accordion.querySelector('[data-accordion="button"]');
      button.addEventListener('click', () => {
        if (this.active !== i) {
          if (this.active !== null) this.accordions[this.active].classList.remove('is-open');
          this.active = i;
          accordion.classList.add('is-open');
        } else {
          this.accordions[this.active].classList.remove('is-open');
          this.active = null;
        }
      });
    });

    this.buttonUploadMore.addEventListener('click', () => {
      this.accordionsCols[1].style.display = 'block';
      this.buttonUploadMore.parentElement.style.display = 'none';
    });
  }
}
