export default class promocode {

  constructor(currentTag, parentNode = document) {
    this.promocode = parentNode.querySelector(currentTag);
  }

  init() {
    if (this.promocode) this._promocodeInit();
    return this;
  }

  options({ cb }) {
    if (cb) this.cb = cb;
    return this;
  }

  _promocodeInit() {
    this.input = this.promocode.querySelector('[data-promocode="input"]');
    this.buttonSubmit = this.promocode.querySelector('[data-promocode="button"]');
    this.buttonClear = this.promocode.querySelector('[data-promocode="clear"]');

    if (this.buttonSubmit) {
      this.buttonSubmit.addEventListener('click', () => {
        const code = this.input.value;
        if (code) {
          this.promocode.classList.add('is-loading');
          this._fetchData(code)
            .then((resp) => {
              this.promocode.classList.add('is-success');
              this.cb(resp.discount);
            })
            .catch(() => this.promocode.classList.add('is-error'))
            .finally(() => this.promocode.classList.remove('is-loading'));
        }
      });
    }

    if (this.buttonClear) {
      this.buttonClear.addEventListener('click', () => {
        this.input.value = '';
        if (this.promocode.classList.contains('is-success')) {
          this.promocode.classList.remove('is-success');
          this.cb(0);
        } else this.promocode.classList.remove('is-error');
      });
    }
  }

  _fetchData(code) {
    return new Promise((resolve, reject) => {
      // тут запрос на бек
      const timerId = setTimeout(() => {
        if (Math.random() > 0.5) resolve({ discount: 10 });
        else reject({ message: 'Неверный код' });
        clearTimeout(timerId);
      }, 1500);
    });
  }
}
