import Tabs from './elements/tabs';
import InputRange from './elements/inputRange';
import RadioGroup from './elements/radioGroup';
import Promocode from './elements/promocode';
import Price from './elements/price';
import getModalOrderHtml from './elements/getModalOrderHtml';
import FixedElement from './elements/fixedElement';

const order = () => {

  const addZero = num => {
    return num <= 9 ? '0' + num : num;
  }

  const form = document.querySelector('.detox__order-form');

  const orderInput = document.querySelector('.order__input[type="date"]');
  const orderInputTel = document.querySelector('.order__input[type="tel"]');
  const orderInputStreet = document.querySelector('.order__input[name="street"]');
  const orderInputAppart = document.querySelector('.order__input[name="apartment"]');
  const detoxOrderForm = document.querySelector('.detox__order-form');
  const formBtn = form.querySelector('.btn[type="submit"]');
  const date = new Date();
  const fullDate = `${addZero(date.getDate() + 1)}.${addZero(date.getMonth() + 1)}.${date.getFullYear()}`;
  const changeDataTtype = fullDate.split('.');
  const reverseData = changeDataTtype.reverse();
  const trueDataType = reverseData.join('-');
  orderInput.min = trueDataType;

  detoxOrderForm.addEventListener('input', (e) => {
    if (orderInputTel.value != 0 && orderInputStreet.value != 0 && orderInputAppart.value != 0 ) {
      formBtn.disabled = false;
    }
  })

  const order = document.querySelector('.js-order');

  if (!order) return;

  // Общая стоимость без скидки
  let cost = 0;

  const currentOrder = {
    deliveryMethod: 'Доставка',
    basket: [],
    discount: 0,
  };

  const modalOrder = document.querySelector('#currentOrder');

  order.addEventListener('submit', (e) => {
    e.preventDefault();
  });

  const buttonSubmit = order.querySelector('.js-order-submit');
  buttonSubmit.addEventListener('click', (e) => {
    console.log('currentOrder.deliveryMethod', currentOrder.deliveryMethod);
    const checkInput = input => {
      let result = true;
      if (currentOrder.deliveryMethod === 'Доставка') {
        result = input.value !== '' && input.value.length >= 3;
        if (input.getAttribute('name') === 'phone') result = input.value !== '+375 ';
      }
      else if (!['street', 'house'].includes(input.getAttribute('name'))) result = input.value !== '';
      return result;
    }
    let valid = true;
    requiredInputs.forEach(input => {
      if (!checkInput(input)) {
        valid = false;
        input.parentNode.classList.add('order__card-input--error');
      }
    })
    if (!valid) {
      errorMessageBlock.classList.remove('is-hide');
      return;
    }

    const data = new FormData(order);
    const date = new Date();

    let hour = data.get('hour');
    const minutes = data.get('minutes');

    currentOrder.number = 21324,
    currentOrder.date = `${addZero(date.getDate())}.${addZero(date.getMonth() + 1)}.${date.getFullYear()}`;
    currentOrder.deliveryCost = cost < 12 ? 3 : 0;
    currentOrder.address = `${data.get('street')}, ${data.get('house')}, ${data.get('apartment')}`;
    currentOrder.time = `${hour}:${minutes} - ${minutes == '30' ? +hour + 1 : hour}:${minutes == '30' ? '00': '30'}`;
    currentOrder.paymethod = data.get('pay-method');
    currentOrder.phone = data.get('phone');
    currentOrder.comments = data.get('comments');
    currentOrder.price = cost;

    modalOrder.querySelector('.modal__body').innerHTML = getModalOrderHtml(currentOrder);
    modalOrder.querySelector('.js-modal-order-submit').addEventListener('click', () => {
      // тут отправить данные на сервер
      console.log('submit', data, currentOrder);
    });

    window.MODAL.showModal(document.querySelector('#currentOrder'));
  });

  const errorMessageBlock = order.querySelector('.js-oder-error-message');

  // required inputs
  const requiredInputs = Array.prototype.slice.call(order.querySelectorAll('input[required]'));
  requiredInputs.forEach(input => {
    input.addEventListener('input', () => {
      if (input.value) {
        input.parentNode.classList.remove('order__card-input--error');
        errorMessageBlock.classList.add('is-hide');
      }
    });
  });

  // init price
  const price = new Price('.js-order-price');
  price.options({ cb: validateForm.bind(this) }).init();

  // Basket
  const basketList = Array.prototype.slice.call(order.querySelectorAll('.js-order-basket-item'));

  // const basketListCount = order.querySelector('.js-order-basket-count');
  // basketListCount.innerText = basketList.length;

  // const decreaseBasketListCount = () => {
  //   const count = parseInt(basketListCount.innerText);
  //   count > 0 ? basketListCount.innerText = count - 1 : null;
  // }

  const basketListClear = order.querySelector('.js-order-basket-clear');
  basketListClear.addEventListener('click', () => {
    if (basketList && basketList.length) {
      basketList.forEach(item => {
        item.querySelector('.field-num__input').value = 0;
        item.classList.add('is-hide');
        // decreaseBasketListCount();
      });
      price.change(0);
    }
  });

  basketList.forEach((item, i) => {

    // const inputFieldNum = item.querySelector('.js-order-basket-field-num');
    const inputFieldNum = item.querySelector('.field-num__input');
    const itemPrice = item.querySelector('[data-basket="cost"]');

    let value = inputFieldNum.value;
    const onePrice = parseFloat(itemPrice.innerText.replace(',', '.')) / value;

    cost += onePrice * value;

    const setPrice = (newValue) => {
      if (newValue === value) return;
      cost -= onePrice * value;
      value = newValue;
      itemPrice.innerText = (onePrice * value).toFixed(2);
      cost += onePrice * value;
      currentOrder.basket[i].count = value;
      currentOrder.basket[i].price = onePrice * value;
      price.change(cost);
    }

    item.addEventListener('click', e => {
      if (e.target.closest('[data-basket="remove"]')) {
        item.classList.add('is-hide');
        // item.parentNode.remove(item);
        // decreaseBasketListCount();
        cost -= onePrice * value;
        value = inputFieldNum.value = 0;
        price.change(cost);
        currentOrder.basket[i].status = false;
      }
      if (e.target.closest('[data-basket="actions"]')) {
        setPrice(inputFieldNum.value);
      }
    });

    currentOrder.basket.push({
      status: true,
      name: item.querySelector('[data-basket="name"]').innerText || '',
      weight: item.querySelector('[data-basket="weight"]').innerText || '',
      count: value,
      price: onePrice * value,
    })
  });

  price.change(cost);

  const deliveryBlock = order.querySelector('.js-order-delivery');

  const deliveryCb = active => {
    currentOrder.deliveryMethod = ['Доставка', 'Самовывоз'][active];
    console.log('deliveryCb', active, currentOrder.deliveryMethod);
    if (active === 1) deliveryBlock.classList.add('is-hide');
    else deliveryBlock.classList.remove('is-hide');
  }

  // tabs init
  const deliveryTabs = new Tabs('.js-delivery');
  deliveryTabs.options({ cb: deliveryCb }).init();

  const modalChangeAddressCb = (response) => {
    // тут данные из модалки
    if (response.deliveryMethod === 'Доставка') {
      ['street', 'house', 'apartment', 'entrance', 'frame', 'floor'].forEach(name => {
        order.querySelector(`input[name="${name}"]`).value = response.data.get(name);
      });

      setTimeout(() => {
        deliveryTabs.toggleTabs(0);
      }, 200);
    } else {
      const oldAddress = order.querySelector('input[name="address"]:checked');
      oldAddress.removeAttribute('checked');
      oldAddress.parentNode.parentNode.classList.add('is-hide');

      const newAddress = order.querySelector(`input[value="${response.data.get('modal-address')}"]`);
      newAddress.setAttribute('checked', 'checked');
      newAddress.parentNode.parentNode.classList.remove('is-hide');
    }
    window.ModalChangeAddress.options({ cb: null });
  }

  let warningDidShow = false;

  const showModalChangeAddress = () => {
    const actions = [
      { name: 'title', status: false },
      { name: 'info', status: false },
    ];
    window.ModalChangeAddress
      .options({ cb: modalChangeAddressCb })
      .setActions({ actions, buttonText: 'Изменить адрес' });
    window.MODAL.showModal(document.querySelector('#changeAddress'));
  }

  const linkChangeAddress = order.querySelector('.js-order-change-address');
  linkChangeAddress.addEventListener('click', () => {
    if (!warningDidShow) {
      const modalWarning = document.querySelector('#warningChangeOrder');
      const buttonCb = modalWarning.querySelector('[data-modal="cb"]');
      buttonCb.addEventListener('click', () => {
        window.MODAL.closeAllModals();
        showModalChangeAddress();
      });
      // скрываем предупреждение после первого показа (может и не надо)
      warningDidShow = true;
      window.MODAL.showModal(modalWarning);
    } else showModalChangeAddress();
  });

  const deliveryTimeTabs = new Tabs('.js-delivery-time');
  deliveryTimeTabs.options({}).init();

  // init InputRange
  const inputRange = new InputRange('.js-order-input-range');
  inputRange.init();

  // init radio-groupe
  const radioGroup = new RadioGroup('.js-order-pay-method-radiogroup');
  radioGroup.init();

  // PROMOCODE
  const cbPromocode = (discount) => {
    price.setDiscount(discount);
    currentOrder.discount = discount;
  }

  // init promocode
  const promocode = new Promocode('.js-order-input-promocode');
  promocode.options({ cb: cbPromocode }).init();

  function validateForm() {
    if (cost > 12 ) {
      buttonSubmit.classList.remove('btn--disabled');
    } else {
      buttonSubmit.classList.add('btn--disabled');
    }
  }

  const sidebar = new FixedElement('.js-order-sidebar');
  sidebar.init();

 
};

export default order;
