export default class basketActions {

  constructor(parentNode = document) {
    this.parentNode = parentNode;
    this.basket = [];
    this.buttonJsClass = '.js-btn-cart';
  }

  init() {
    this._getCurrentBasket()
      .then(resp => {
        if (resp && resp.length) this.basket = [...resp];
        this._initProductButtons();
      });
    return this;
  }

  options({ buttonJsClass, cb }) {
    if (cb) this.cb = cb;
    if (buttonJsClass) this.buttonJsClass = buttonJsClass;
    return this;
  }

  _initProductButtons() {
    this.productButtons = Array.prototype.slice.call(this.parentNode.querySelectorAll(this.buttonJsClass));
    this.productButtons.forEach(button => {
      button.addEventListener('click', () => {
        if (!this.basket.length) {
          const actions = [
            { name: 'title', status: true },
            { name: 'info', status: false }, // показать предупреждение: "при изменении адреса или типа доставки ..."
          ];
          // если вход не выполнен - добавить переход на модалку входа или регистрации
          const authStatus = false;
          if (!authStatus) {
            actions.push({ name: 'enter', status: true });
          }
          window.ModalChangeAddress
            .options({ cb: null })
            .setActions({ actions, buttonText: 'Сохранить адрес' });
          window.MODAL.showModal(document.querySelector('#changeAddress'));
        }
      });
    });
  }

  _getCurrentBasket() {
    return new Promise((resolve, reject) => {
      // тут запрос на бек за корзиной если авторизован
      const timerId = setTimeout(() => {
        const fakeBasket = [
          // {
          //   productId: 2517,
          //   name: 'Итальянский минестроне',
          //   image: 'assets/images/content/soup/soup1.jpg',
          //   weight: '300ml',
          //   price: 5.90,
          //   count: 1,
          // }
        ];
        resolve(fakeBasket);
        clearTimeout(timerId);
      }, 300);
    });
  }

  _postCurrentBasket() {
    return new Promise((resolve, reject) => {
      // тут запрос на бек добавить / изменить корзину
      const timerId = setTimeout(() => {
        const fakeBasket = [{
          productId: 2517,
          name: 'Итальянский минестроне',
          image: 'assets/images/content/soup/soup1.jpg',
          weight: '300ml',
          price: 5.90,
          count: 1,
        }]
        resolve(fakeBasket);
        clearTimeout(timerId);
      }, 300);
    });
  }
}
