export default class Tabs {

  constructor(currentTag, parentNode = document) {
    this.currentTag = currentTag;
    this.tabs = parentNode.querySelector(this.currentTag);
  }

  init() {
    if (this.tabs) this._tabsInit();
    return this;
  }

  options({ cb }) {
    if (cb) this.cb = cb;
    return this;
  }

  _tabsInit() {
    this.tabLink = Array.prototype.slice.call(this.tabs.querySelectorAll('[data-tabs="link"]'));
    this.content = this.tabs.querySelector('[data-tabs="content"]');

    this.active = this.tabLink.findIndex(el => el.classList.contains('active'));

    // default settings
    this.content.style.width = `${this.tabLink.length * 100}%`;
    this.toggleTabs();

    this.tabLink.forEach((link, i) => {
      link.addEventListener('click', () => {
        this.tabLink.find(el => el.classList.contains('active')).classList.remove('active');
        link.classList.add('active');
        this.active = i;
        if (this.cb) this.cb(this.active);
        this.toggleTabs();
      });
    });
  }

  toggleTabs(active = this.active) {
    if (active !== this.active) this.active = active;
    this.content.style.transform = `translate3d(-${100 / this.tabLink.length * active}%, 0, 0)`;
  }

  getActive() {
    return this.active;
  }
}
