const initBurger = () => {

  const burgers = document.querySelectorAll('.burger');
  if (burgers) {

    for (let i = 0; i < burgers.length; i++) {
      let burger = burgers[i];
      burger.addEventListener('click', showBurgerTarget);
    }

    function showBurgerTarget() {
      const targetId = this.getAttribute('data-target-id');
      const targetClassToggle = this.getAttribute('data-target-class-toggle');
      const basket = document.querySelector('.js-basket');

      if (basket) {
        if (basket.classList.contains('is-show')) {
          basket.classList.remove('is-show');
        }
      }

      if (targetId && targetClassToggle) {
        this.classList.toggle('burger--close');

        document.querySelector(`.${targetId}`).classList.toggle(targetClassToggle);
      }
    }
  }
};

export default initBurger;
