export default class inputRange {

  constructor(currentTag, parentNode = document) {
    this.currentTag = currentTag;
    this.inputRange = parentNode.querySelector(this.currentTag);
  }

  init() {
    if (this.inputRange) this._inputRangeInit();
    return this;
  }

  _inputRangeInit() {
    this.input = this.inputRange.querySelector('[data-input-range="input"]');
    this.value = this.inputRange.querySelector('[data-input-range="value"]');

    const min = this.input.getAttribute('min') || 0;
    const max = this.input.getAttribute('max') || 100;

    this.input.addEventListener('change', e => {
      this.value.innerText = e.target.value;
    });

    this.value.addEventListener('keypress', function (e) {
      if (isNaN(String.fromCharCode(e.which))) e.preventDefault();
    });

    this.value.addEventListener('input', e => {
      let res = parseInt(this.value.innerText) || 0;
      if (res > max) res = max;
      if (res < min) res = min;
      this.input.value = res;
      this.value.innerText = res;

      this.setCaretToEnd(this.value);
    });
  }

  setCaretToEnd(target) {
    const range = document.createRange();
    const sel = window.getSelection();
    range.selectNodeContents(target);
    range.collapse(false);
    sel.removeAllRanges();
    sel.addRange(range);
    target.focus();
    range.detach();
  }
}
